.nav__toggle {
  display: block;
  position: relative;
  top: -5px;
  width: 20px;
  height: 2px;
  margin-right: 30px;
  background-color: #000000;
  cursor: pointer;
  border: none;
  z-index: 5;
  transition: all 0.3s ease 0s;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}
.nav__toggle::before {
  content: "";
  position: absolute;
  top: 5px;
  left: 0px;
  width: 20px;
  height: 2px;
  background-color: #000000;
  cursor: pointer;
  border: none;
  z-index: 5;
  transition: all 0.3s ease 0s;
}
.nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 0px;
  width: 20px;
  height: 2px;
  background-color: #000000;
  cursor: pointer;
  border: none;
  z-index: 5;
  transition: all 0.3s ease 0s;
}
.nav__toggle--closed {
  top: 2px;
  height: 20px;
  background-color: #FFFFFF;
}
.nav__toggle--closed::before,
.nav__toggle--closed::after {
  content: "";
  position: absolute;
  top: 8px;
  left: -2px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  border-radius: 1.5px;
  transform: matrix(0.71, -0.73, 0.69, 0.71, 0, 0);
}
.nav__toggle--closed::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.nav__toggle--closed::after {
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .nav__toggle--closed {
    display: none;
  }
}
